header {
  background: green;
  padding: 0;
  margin: 0;
  background-image: -webkit-linear-gradient(left, #1e5794, #006b9e, #007c95, #008a7e, #229460);
  background-image: linear-gradient(to right, #1e5794, #006b9e, #007c95, #008a7e, #229460);
}

.header__main-heading {
  text-align: center;
  padding: 25px 0px;
  color: white;
  font-size: 38px;
}

@media screen and (max-width: 512px) {
  .header__main-heading {
    font-size: 28px;
  }
}
.searchfield {
  color: black;
  text-align: center;
  display: flex;
  width: 90%;
  margin: 20px auto;
}

.searchfield__input {
  width: 90%;
  border: 1px solid black;
  padding: 15px 20px;
  font-size: 18px;
}

.searchfield__button {
  width: 55px;
  margin-left: 20px;
  background-color: #24995D;
  border: none;
}

.searchfield__icon {
  font-size: 28px;
  color: white;
  -webkit-transform: scale(-1, 1);
  transform: scale(-1, 1);
  margin-top: 3px;
}

@media screen and (max-width: 512px) {
  .searchfield__input {
    font-size: 15px;
  }

  .searchfield__button {
    width: 50px;
    margin-left: 13px;
  }

  .searchfield__icon {
    font-size: 23px;
  }
}
.current-items {
  width: 90%;
  margin: 0 auto;
}

.current-items__no-items {
  text-align: center;
  margin: 40px 0px;
  margin-bottom: 50px;
}

.current-items__sadsmile {
  font-size: 95px;
}

.current-items__sadtext {
  font-size: 18px;
  margin-top: 7px;
}
.waste-item {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  margin-bottom: 35px;
}

.waste-item__title {
  display: flex;
  width: 30%;
}

.waste-item__title-text {
  width: 90%;
  margin-left: 15px;
}

.waste-item__star {
  margin-top: 3px;
}

.waste-item__favourite {
  color: #24995D;
}

.waste-item__body {
  width: 50%;
}

.waste-item__body li {
  margin-bottom: 20px;
}

@media screen and (max-width: 624px) {
  .waste-item {
    display: block;
    margin-bottom: 45px;
    text-align: center;
  }

  .waste-item__title {
    width: 95%;
    display: block;
    max-width: 350px;
    margin: 0 auto;
    text-align: center;
    font-weight: bold;
  }

  .waste-item__title-text {
    width: 95%;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 20px;
  }

  .waste-item__body {
    width: 95%;
    margin: 0 auto;
    max-width: 340px;
  }

  .waste-item__body li {
    display: block;
    text-align: center;
  }
}
.sk-cube-grid {
  width: 50px;
  height: 50px;
  margin: 100px auto;
}

.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #333;
  float: left;
  -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
          animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out; 
}

.sk-cube-grid .sk-cube1 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s; }
.sk-cube-grid .sk-cube2 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s; }
.sk-cube-grid .sk-cube3 {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s; }
.sk-cube-grid .sk-cube4 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s; }
.sk-cube-grid .sk-cube5 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s; }
.sk-cube-grid .sk-cube6 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s; }
.sk-cube-grid .sk-cube7 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s; }
.sk-cube-grid .sk-cube8 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s; }
.sk-cube-grid .sk-cube9 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s; }

@-webkit-keyframes sk-cubeGridScaleDelay {
  0%, 70%, 100% {
    -webkit-transform: scale3D(1, 1, 1);
            transform: scale3D(1, 1, 1);
  } 35% {
    -webkit-transform: scale3D(0, 0, 1);
            transform: scale3D(0, 0, 1); 
  }
}

@keyframes sk-cubeGridScaleDelay {
  0%, 70%, 100% {
    -webkit-transform: scale3D(1, 1, 1);
            transform: scale3D(1, 1, 1);
  } 35% {
    -webkit-transform: scale3D(0, 0, 1);
            transform: scale3D(0, 0, 1);
  } 
}
.favourites {
  background-color: #F7FEFA;
}

.favourites__content {
  width: 90%;
  margin: 0 auto;
  padding: 20px 0px; 
}

.favourites__heading {
  margin-bottom: 30px;
  color: #24995D;
}

@media screen and (max-width: 624px) {
  .favourites__heading {
    text-align: center;
  }
}
footer {
  text-align: center;
  padding: 20px 0px;
}

.footer_info a {
  text-decoration: none;
}
* {
  margin: 0;
  padding: 0;
}

.no-items {
  text-align: center;
  margin: 40px 0px;
  margin-bottom: 30px;
}

.no-items__icon {
  font-size: 95px;
}

.no-items__text {
  font-size: 18px;
  margin-top: 7px;
}
